// main page elements
body {
	font-size: 14px;
	line-height: 18px;
	color: #020203;
	font-family: 'Space Grotesk', sans-serif;
	font-weight: 300;
	background: white;
	overflow-x: hidden;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden; }

.container {
	max-width: 1170px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1200px) {
		padding: 0 15px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 10px; } }

.flex {
	display: flex; }

.btn {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 50px;
	font-weight: bold;
	font-size: 18px;
	line-height: 23px; }

.grow {
	background: url(../img/img.png);
	background-repeat: no-repeat;
	margin-bottom: 105px;
	&__container {
		max-width: 1295px;
		width: 100%;
		margin: 0 auto;
		padding-bottom: 180px; } }
.grow__main {
	gap: 47px;
	align-items: center;
	.grow__text {
		.grow__heading {
			margin-bottom: 58px;
			position: relative;
			h1 {
				font-weight: bold;
				font-size: 95px;
				line-height: 94.6%;
				color: $yellow;
				text-transform: uppercase;
				span {
					color: white; } }
			.sub-h1 {
				position: absolute;
				font-size: 95px;
				line-height: 94.6%;
				top: 0;
				left: 21px;
				font-weight: bold;
				color: rgba(255, 255, 255, 0.1);
				color: transparent;
				-webkit-text-stroke: 1px rgba(255, 255, 255, 0.1);
				text-transform: uppercase; } }
		p {
			font-size: 20px;
			line-height: 26px;
			max-width: 641px;
			width: 100%;
			color: white;
			margin-bottom: 50px; }
		.grow__btns {
			display: flex;
			align-items: center;
			gap: 30px;
			.btn {
				max-width: 255px;
				background: inherit;
				border: 1px solid $yellow;
				color: $yellow; }
			.btn-full {
				color: black;
				background: $yellow; } } }
	.grow__images {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		a {
			position: absolute; } } }
